* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Josefin Sans", "Arial Narrow";
}
.header{
  background-color: #f5ba13;
  width: 100%;
  line-height: 50px;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px -5px black;

}
.main_note{
 width: 500px;
 padding: 15px 10px 5px 10px;
 margin: 20px auto;
}
.form{
  min-height: 50px;
  color: white;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px black;
  border-radius: 10px;
  position: relative;
}
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  font-weight: bold;
}
textarea{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}
.plus_sign{
 padding: 1.5%;
  border-radius: 50px;
  background-color: #f5ba13;
  /* transform: translate(900%,60%); */
  left: 83%;
  top: 83%;
  position: absolute;
  
  
}
.note{
  background:#fff;
  border-radius: 7px;
  box-shadow: 0px 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f4b400;
  border: none;
  width: 50;
  height: 50;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, alpha);
  border-radius: 50%;


}
.note .btn:hover{
  background-color: #f5ba13;
}
.deleteIcon {
  /* width: 0.7em;
  height: 0.7em; */
  margin-bottom: 10px;
}
.note .btn:hover .deleteIcon{
  color: #fff;
}
.footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  text-align: center;
  font-weight: bold;
  
}